.paginationContainer {
  height: 50px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.paginationContainer * {
  width: 30px;
  height: 30px;
  font-size: 15px;
  user-select: none;
  cursor: pointer;
  text-align: center;
  border-radius: 50%;
  border: none;
  outline: none;
  background: none;
}

.activePage {
  background-color: #aaa;
}

.paginationItem {
  width: 20px;
  height: 20px;
  font-weight: 12px;
}
